#app3 {
    border: 1px solid green;
}

#app3 .square {
    width: 10vw;
    height: 10vw;
    border: 1px solid green;
    margin-top: 10vh;
    margin-left: 10vw;
    transition: transform 1s, border-radius 1s;
}

#app3 .square.active {
    transform: translateX(15vw);
    border-radius: 50%;
}